import React from 'react';
import Typist from 'react-typist';

class Signature extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      typing: true
    }
  }
done = () => {
  this.setState({ typing: false }, () => {
    this.setState({ typing: true })
  });
}

  render() {
    return (
      <div>
        <div className="triangle" />
        {this.state.typing
            ? <Typist className="signature" avgTypingDelay={60} onTypingDone={this.done}>
              <span>Software Engineering</span>
              <Typist.Backspace count={20} delay={2000} />
              <span>Powered by HTML/CSS/Javascript</span>
              <Typist.Backspace count={30} delay={2000} />
              <span>Peter Zhu</span>
              <Typist.Backspace count={9} delay={2000} />
              </Typist>
            : ''
          }
      </div>
    );
  }
}

export default Signature;
